import { v4 as uuid } from 'uuid';
import { SET_ALERT, REMOVE_ALERT } from './types';

//don't have to import thunk here. used in store.js. => =>is just a curried function.
//https://olinations.medium.com/creating-a-function-factory-using-javascript-currying-3e4038b64719
//https://daveceddia.com/what-is-a-thunk/
//https://stackoverflow.com/questions/32782922/what-do-multiple-arrow-functions-mean-in-javascript
export const setAlert = (msg, alertType, timeout = 5000) => (dispatch) => {
  const id = uuid();
  dispatch({
    type: SET_ALERT,
    payload: {
      msg,
      alertType,
      id,
    },
  });

  setTimeout(() => {
    dispatch({ type: REMOVE_ALERT, payload: id });
  }, timeout);
};
