import {
  GET_POSTS,
  POST_ERROR,
  CHANGE_PAGE,
  LOADING,
  DELETE_POST,
  GET_POST,
  ADD_COMMENT,
  REMOVE_COMMENT,
} from '../actions/types';

const initialState = {
  count: 0,
  posts: [],
  post: null,
  loading: true,
  error: {},
  current: 1,
};

export default function (state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    case GET_POSTS:
      return {
        ...state,
        current: payload.page,
        posts: payload.currentposts.docs,
        count: payload.count,
        loading: false,
      };
    case CHANGE_PAGE:
      return {
        ...state,
        posts: payload.currentposts.docs,
        current: payload.page,
        loading: false,
      };
    case GET_POST:
      return {
        ...state,
        post: payload,
        loading: false,
      };
    case DELETE_POST: {
      return {
        ...state,
        posts: state.posts.filter((post) => post._id !== payload),
        loading: false,
      };
    }
    case POST_ERROR:
      return {
        ...state,
        error: payload,
        loading: false,
      };
    case LOADING:
      return {
        ...state,
        loading: true,
      };
    case ADD_COMMENT:
      return {
        ...state,
        post: { ...state.post, comments: payload },
        loading: false,
      };
    case REMOVE_COMMENT:
      return {
        ...state,
        post: {
          ...state.post,
          comments: state.post.comments.filter(
            (comments) => comments._id !== payload
          ),
        },
        loading: false,
      };
    default:
      return state;
  }
}
