import './App.css';
import React, { Fragment, useEffect } from 'react';
import Navbar from './componets/layout/navbar';
import Landing from './componets/layout/landing';
import Register from './componets/auth/register';
import Login from './componets/auth/login';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Alert from './componets/layout/alert';
import Post from './componets/post/post';
import getPosts from './componets/posts/getPosts';
import sendPost from './componets/posts/sendPost';
import PrivateRoute from './componets/routing/privateRoute';
//redux
import { Provider } from 'react-redux';
import store from './store';
import setAuthToken from './utils/setAuthToken';
import { loadUser } from './actions/auth';

if (localStorage.token) {
  setAuthToken(localStorage.token);
}

const App = () => {
  useEffect(() => {
    store.dispatch(loadUser());
  }, []);

  return (
    //2nd fragment can't use section or blank.
    <Provider store={store}>
      <Router basename='app'>
        <Fragment>
          <Navbar />
          <Alert />
          <Route exact path='/' component={Landing} />
          <Fragment>
            <Switch>
              <Route exact path='/register' component={Register} />
              <Route exact path='/login' component={Login} />
              <PrivateRoute exact path='/send' component={sendPost} />
              <Route exact path='/posts/:type' component={getPosts} />
              <Route exact path='/posts/' component={getPosts} />
              <Route exact path='/posts/:type/:id' component={Post} />
              {/* backend 好像不管用 //// 教学可以  */}
            </Switch>
          </Fragment>
        </Fragment>
      </Router>
    </Provider>
  );
};

export default App;
