import React, { useState, Fragment, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { getPosts, changePage, loadingf } from '../../actions/post';
import Spinner from '../layout/spinner';
import PostItem from './postItem.js';

import './getPosts.css';

//get posts of a certain type
const GetPosts = ({
  loadingf,
  getPosts,
  changePage,
  post: { posts, loading, current, count },
  zipCode,
  match,
}) => {
  const [formData, setFormData] = useState({
    pricemin: '',
    pricemax: '',
    yearmin: '',
    yearmax: '',
    odomax: '',
    sorted: false,
  });

  //搜索参数
  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
    console.log(formData);
  };

  const { pricemax, pricemin, yearmin, yearmax, odomax } = formData;

  //进入页面按类加载
  if (match) {
    var type = match.params.type;
  } else type = null;
  useEffect(() => {
    loadingf(); //important
    const query = JSON.stringify({ zipCode, type, fil: null, page: 1 });
    console.log(getPosts); //without passing in to function, get itself.
    getPosts({ query });
    // Line 60:6:  React Hook useEffect has missing dependencies: 'current', 'loadingf',
    //and 'type'. Either include them or remove the dependency array. If 'loadingf'
    //changes too often, find the parent component that defines it and wrap
    // that definition in useCallback  react-hooks/exhaustive-deps
  }, []);
  //[getPosts, loadingf, current, type]:重复加载。

  //发送搜索请求
  const sortPost = () => {
    loadingf();
    const query = JSON.stringify({
      zipCode,
      fil: {
        pricemax,
        pricemin,
        yearmin,
        yearmax,
        odomax,
      },
      type,
      page: 1,
    });
    setFormData({ ...formData, sorted: true });
    getPosts({ query });
  };

  //翻页
  const changeP = (nop) => {
    loadingf();
    const query = JSON.stringify({
      zipCode,
      type: type,
      fil: formData.sorted
        ? {
            pricemax,
            pricemin,
            yearmin,
            yearmax,
            odomax,
          }
        : null,
      page: current + nop,
    });
    changePage({ query });
  };

  return loading || posts.length === null ? (
    <Spinner />
  ) : (
    <section className='container'>
      <p className='hp'>
        <span className='hps'> {type ? type : '所有'}信息</span>{' '}
        <span className='hpss'>
          测试版本仅支持湾区(San Jose, San Francisco,
          Oakland)，其他地区和范围搜索功能陆续上线中
        </span>
        {/* {zipCode}
        <span className='hps'> 附近的{type ? type : '所有'}信息</span> */}
      </p>
      {type === '二手车' ? (
        <div className='sd-1'>
          <div className='sd-2'>
            <p className='sp-1'>
              价格
              <span className='ss-1'>
                <span className='ss-2'>
                  最低{' '}
                  <input
                    name='pricemin'
                    type='text'
                    onChange={(e) => {
                      onChange(e);
                    }}
                    className='sb-1'
                  />{' '}
                </span>
                <span className='ss-3'>
                  最高{' '}
                  <input
                    name='pricemax'
                    onChange={(e) => {
                      onChange(e);
                    }}
                    type='text'
                    className='sb-2'
                  />
                </span>
              </span>
            </p>
            <p className='sp-1'>
              年份
              <span className='ss-1'>
                <span className='ss-2'>
                  最早{' '}
                  <input
                    name='yearmin'
                    onChange={(e) => {
                      onChange(e);
                    }}
                    type='text'
                    className='sb-1'
                  />{' '}
                </span>
                <span className='ss-3'>
                  最晚{' '}
                  <input
                    name='yearmax'
                    onChange={(e) => {
                      onChange(e);
                    }}
                    type='text'
                    className='sb-2'
                  />
                </span>
              </span>
            </p>
            {/* <p className='sp-2'>
              厂商
              <span className='ss-1'>
                <span className='ss-2'>
                  <input type='text' className='sb-2' />
                </span>
              </span>
            </p> */}
            <p className='sp-2'>
              里程
              <span className='ss-1'>
                <span className='ss-2'>
                  最大{' '}
                  <input
                    onChange={(e) => {
                      onChange(e);
                    }}
                    name='odomax'
                    type='text'
                    className='sb-2'
                  />
                </span>
              </span>
            </p>
          </div>
          <div className='sd-3'>
            <button onClick={sortPost}>筛选</button>
          </div>
        </div>
      ) : null}
      {/* Postform */}
      <div className='posts'>
        {posts.map((
          post //?
        ) => (
          <PostItem key={post._id} post={post} />
        ))}
      </div>
      <div className='gd-1'>
        <button
          onClick={() => {
            loadingf(); //important
            const query = JSON.stringify({ zipCode, type, fil: null, page: 1 });
            console.log(getPosts); //without passing in to function, get itself.
            getPosts({ query });
          }}
          className='gb-1'
          disabled={current === 1 ? true : false}
        >
          首页
        </button>
        <button
          onClick={() => changeP(-1)}
          className='gb-1'
          disabled={current === 1 ? true : false}
        >
          上页
        </button>
        第{current}页，共{Math.ceil(count / 5)}页
        <button
          className='gb-2'
          onClick={() => changeP(1)}
          style={{ float: 'right' }}
          disabled={count / 5 >= current ? false : true}
        >
          下页
        </button>
      </div>
    </section>
  );
};

GetPosts.propTypes = {
  getPosts: PropTypes.func.isRequired,
  changePage: PropTypes.func.isRequired,
  post: PropTypes.object.isRequired,
  zipCode: PropTypes.number.isRequired,
  loadingf: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  post: state.post,
  zipCode: state.zipCode.zipCode,
});
export default connect(mapStateToProps, { getPosts, changePage, loadingf })(
  GetPosts
);
