import ImageGallery from 'react-image-gallery';
import React, { Fragment, useCallback } from 'react';
import 'react-image-gallery/styles/css/image-gallery.css';
import './index.css';
// import image1 from '../../../../img/image1.jpg';
// import image2 from '../../../../img/image2.jpg';
// import image3 from '../../../../img/image3.jpg';
// import image4 from '../../../../img/image4.jpg';
// import image5 from '../../../../img/image5.jpg';
import { PROXY } from '../../../../utils/consts';
import { connect } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';

//这里之前是post，错了，应该是prop
const Index = (prop) => {
  // const images = [
  //   {
  //     original: image1,
  //   },
  //   {
  //     original: image2,
  //   },
  //   {
  //     original: image3,
  //   },
  //   {
  //     original: image4,
  //   },
  //   {
  //     original: image5,
  //   },
  // ];
  const posts = prop.post.posts;
  const images = [];

  //https://stackoverflow.com/questions/29244731/react-router-how-to-manually-invoke-link
  const history = useHistory();

  //https://stackoverflow.com/questions/29244731/react-router-how-to-manually-invoke-link

  const onClick = (e) => {
    const string = e.target.src.slice(PROXY.length + 1);
    let index;
    for (let i = 0; i <= posts.length; i++) {
      if (posts[i].imageUrl[0].url === string) {
        index = i;
        break;
      }
    }
    history.push(`/posts/${posts[index].type}/${posts[index]._id}`);
  };

  posts.forEach((post) =>
    images.push({
      original: post.imageUrl[0] ? PROXY + '/' + post.imageUrl[0].url : null,
    })
  );
  return (
    <div className='imgwrapper'>
      <ImageGallery
        onClick={(e) => onClick(e)}
        items={images}
        showFullscreenButton={false}
        showPlayButton={false}
        showNav={false}
        showBullets={true}
        showThumbnails={false}
        autoPlay={true}
      />
    </div>
  );
};

const mapStateToProps = (state) => ({
  post: state.post,
});

export default connect(mapStateToProps)(Index);
