import React, { Fragment } from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import Moment from 'react-moment';
import { connect } from 'react-redux';
import { deletePost } from '../../actions/post';
import { PROXY } from '../../utils/consts';
import path from 'path';

const PostItem = ({
  // showButton = true, vs propDefault?
  deletePost,
  auth,
  post: {
    _id,
    text,
    name,
    user,
    comments,
    postDate,
    imageUrl,
    type,
    price,
    year,
    odo,
    make,
  },
}) => {
  // console.log(user === auth.user._id);
  return (
    <div
      style={{
        border: '1px solid rgb(200,200,200',
        position: 'relative',
        marginTop: 20,
      }}
    >
      {!auth.loading && user === (auth.user ? auth.user._id : null) && (
        <i
          class='fas fa-backspace'
          onClick={() => deletePost(_id)}
          style={{
            top: 10,
            right: 10,
            position: 'absolute',
            color: 'rgb(200,200,200',
          }}
        ></i>
      )}

      <div
        className='post bg-white p-1 my-1'
        style={{ border: 'none', marginBottom: 0 }}
      >
        <div>
          <img
            src={imageUrl[0] ? PROXY + '/' + imageUrl[0].url : null}
            alt=''
          />
        </div>
        <div>
          <p className='my-10'>{text}</p>

          <p className='post-date'>
            {name} 发布于：
            <Moment format='YYYY/MM/DD  h:mm:ss a'>{postDate}</Moment>
          </p>

          {type === '二手车' ? (
            <p className='post-date'>
              年份：{year} 价格：{price} 里程：{odo} 厂商：{make}
            </p>
          ) : null}

          <Link to={`/posts/${type}/${_id}`} className='btn btn-primary'>
            查看详情
          </Link>
        </div>
      </div>
    </div>
  );
};

PostItem.propTypes = {
  post: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,
  deletePost: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({
  auth: state.auth,
});

export default connect(mapStateToProps, { deletePost })(PostItem);
