import { CHANGE_ZIPCODE } from '../actions/types';

const initalState = { zipCode: '00000' };

export default function zip(state = initalState, action) {
  const { type, payload } = action;
  switch (type) {
    case CHANGE_ZIPCODE:
      return { zipCode: payload.zip };
    default:
      return state;
  }
}
