import axios from 'axios';
import React, { useState, Fragment, useEffect } from 'react';
import { Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import { PROXY } from '../../utils/consts';
import { useHistory } from 'react-router-dom';
import { setAlert } from '../../actions/alert';

// import{withRouter } from 'react-router-dom'
// https://reacttraining.com/blog/react-router-v5-1/
// https://stackoverflow.com/questions/42875949/react-router-v4-redirect-not-working
// https://stackoverflow.com/questions/49738249/react-router-v4-redirecting-on-form-submit
// https://ui.dev/react-router-v4-programmatically-navigate/

//必须connect dispatch to props
//t1 is trigger when called, dispatch(...)
const t1 = (data, config, history, type) => async (dispatch) => {
  try {
    const res = await axios.post(`${PROXY}/api/post`, data, config);
    // console.log(setAlert('发布成功,三秒后跳转...', 'success', 3000));
    dispatch(setAlert('发布成功,三秒后跳转...', 'success', 3000));
    setTimeout(() => history.push(`/posts/${type}`), 3000);
  } catch (error) {}
};

const SendPost = ({ t1, setAlert }) => {
  const [formData, setFormData] = useState({
    text: '',
    zipCode: '',
    type: '',
    model: '',
    make: '',
    year: '',
    odo: '',
    names: null,
    files: [],
  });

  // let name;
  // const files = document.querySelector('#file');
  // useEffect(() => {
  //   console.log(files);
  // }, [formData]);
  const { text, zipCode, files } = formData;

  //二手车
  const { price, year, make, odo } = formData;

  // 租房
  //price

  //求助

  //闲置
  //price

  let { type } = formData;
  const history = useHistory();
  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
    // console.log(formData); return last time formdata
    console.log(formData);
  };

  const onSubmit = async (e) => {
    e.preventDefault();

    const newPost = { text, zipCode, year, make, odo, price, type, files };
    if (
      type !== '租房' &&
      type !== '二手车' &&
      type !== '求助' &&
      type !== '闲置'
    ) {
      setAlert('请输入信息种类', 'danger', 3000);
    } else
      try {
        const config = {
          headers: {},
        };
        const data = new FormData();

        files.forEach((file) => data.append('files', file));

        // console.log(data);
        data.append('json', JSON.stringify(newPost));
        // for (let key of data.entries()) {
        //   console.log(key[0] + ', ' + key[1]);
        // }

        console.log(t1);
        t1(data, config, history, type);
        // return <Redirect to='/send' />;
      } catch (err) {
        console.log(err.response.data);
      }
  };

  const deleteFile = (e) => {
    const name = e.target.closest('p').id;
    const newFiles = formData.files.filter((file) => file.name !== name);
    const newNames = newFiles.map((file) => file.name);
    setFormData({ ...formData, names: newNames, files: newFiles });
  };

  const inputChange = (e) => {
    setFormData({
      ...formData,
      files: Array.from(e.target.files),
      //  don't call funcntions in usestate.否则deleteFile传入的formdata永远是这次的状态而不会更新。和closure有关。
      // names: Array.from(e.target.files).map((file) => (
      //   <p key={file.name} id={file.name}>
      //     {file.name} <button onClick={(e) => deleteFile(e)}>test2</button>
      //   </p>
      names: Array.from(e.target.files).map((file) => file.name),
    });
  };

  return (
    <section className='container'>
      <div className='post-form'>
        <div className='bg-primary p'>
          <h3>发布信息</h3>
        </div>
        <form className='form my-1' action='/' onSubmit={(e) => onSubmit(e)}>
          <input
            type='text'
            name='type'
            placeholder='租房/二手车/闲置/求助'
            onChange={(e) => {
              onChange(e);
            }}
          />
          <textarea
            name='text'
            cols='30'
            rows='5'
            placeholder=''
            onChange={(e) => {
              onChange(e);
            }}
            required
          ></textarea>
          <input
            type='text'
            name='zipCode'
            placeholder='ZIPCODE'
            maxLength='5'
            onChange={(e) => {
              onChange(e);
            }}
          />
          {type === '租房' || type === '二手车' ? (
            <input
              type='text'
              name='price'
              placeholder='价格'
              onChange={(e) => {
                onChange(e);
              }}
              required
            />
          ) : null}
          {type === '闲置' ? (
            <input
              type='text'
              name='price'
              placeholder='价格（免费请填0）'
              onChange={(e) => {
                onChange(e);
              }}
              required
            />
          ) : null}
          {type === '二手车' ? (
            <Fragment>
              <input
                type='text'
                name='year'
                placeholder='年份'
                onChange={(e) => {
                  onChange(e);
                }}
              />{' '}
              <input
                type='text'
                name='make'
                placeholder='厂商'
                onChange={(e) => {
                  onChange(e);
                }}
              />
              <input
                type='text'
                name='odo'
                placeholder='里程'
                onChange={(e) => {
                  onChange(e);
                }}
              />
            </Fragment>
          ) : null}
          <input
            className='btn btn-dark my-1'
            type='button'
            value='插入图片（最多10张）'
            onClick={() => document.getElementById('file').click()}
          />

          <input
            type='file'
            style={{ display: 'none' }}
            name='test'
            id='file'
            onChange={(e) => inputChange(e)}
            multiple
          />
          <input type='submit' className='btn btn-dark my-1' value='发布' />
        </form>
      </div>
      {formData.names
        ? formData.names.map((name) => (
            <p key={name} id={name}>
              {name}{' '}
              <i
                onClick={(e) => deleteFile(e)}
                className='far fa-trash-alt'
              ></i>
            </p>
          ))
        : null}
    </section>
  );
};

// const mapStateToProps = (state) => ({
//   user: state.auth.user,
// }); //user is not needed cuz the axios global header.

export default connect(null, { t1, setAlert })(SendPost);
