import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';
import './index.css';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { logout } from '../../../actions/auth';
import { changeZip } from '../../../actions/zipCode';

const navbar = ({
  auth: { isAuthenticated, loading }, //什么语法？
  logout,
  changeZip,
  zipCode,
}) => {
  const authLinks = (
    <ul>
      <li>
        <Link to='/send'>
          <span className=''>发布信息</span>{' '}
        </Link>
      </li>
      <li>
        <Link to='/' onClick={logout}>
          <i className='fas fa-sign-out-alt' />{' '}
          <span className=''>退出登录</span>{' '}
        </Link>
      </li>
    </ul>
  );

  const guestLinks = (
    <ul>
      <li>
        <Link to='/send'>
          <span className=''>发布信息</span>{' '}
        </Link>
      </li>
      <li>
        <Link to='/register'>注册</Link>
      </li>
      <li>
        <Link to='/login'>登录</Link>
      </li>
    </ul>
  );

  const onChange = (e) => {
    changeZip(e.target.value);
  };

  const changeZ = () => {
    document.querySelector('#zip').classList.toggle('editZip');
    // document.querySelector('#zip2').classList.toggle('editZip2');
  };

  const focusOut = () => {
    document.querySelector('#zip').classList.add('editZip');
    // document.querySelector('#zip2').classList.toggle('editZip2');
  };

  return (
    <nav className='navbar bg-dark'>
      <ul id='logo'>
        <li>
          <Link to='/'>
            任意门{' '}
            <span className='ns-2'>
              <i className='far fa-arrow-alt-circle-left'></i> 回主页
            </span>{' '}
          </Link>
          <span className='ns-1'>连接在美华人</span>
          {/* {zipCode}{' '}
          <span className='' id='zip2'>
            <input
              className='editZip'
              type='text'
              id='zip'
              onChange={(e) => onChange(e)}
              onBlur={() => focusOut()}
              maxLength={5}
              style={{
                width: '50px',
                border: 'none',
                background: 'rgb(160,160,160)',
              }}
            />{' '}
            <i
              className='far fa-edit'
              onClick={() => {
                changeZ();
              }}
            />
          </span> */}
        </li>
      </ul>
      {!loading && (
        <Fragment>{isAuthenticated ? authLinks : guestLinks}</Fragment>
      )}
    </nav>
  );
};
navbar.propTypes = {
  logout: PropTypes.func.isRequired,
  auth: PropTypes.object.isRequired,
  zipCode: PropTypes.number.isRequired, //PTNR
};
const mapStateToProps = (state) => ({
  auth: state.auth,
  zipCode: state.zipCode.zipCode,
});

export default connect(mapStateToProps, { logout, changeZip })(navbar);
