import React from 'react';
import './index.css';
import b1 from '../../../../img/b1.jpg';
import b2 from '../../../../img/b2.jpg';
import b3 from '../../../../img/b3.jpg';
import b4 from '../../../../img/b4.jpg';
import bH from '../../../../img/bH.jpg';
import { Fragment } from 'react';

const index = () => {
  return (
    <div className='bannerC'>
      <img className='banners' src={b3} />
      <img className='banners' src={b4} />
      <img className='banners' src={b2} />
      <img className='banners' src={b1} />
      <img className='bannerH' src={bH} />
    </div>
  );
};

export default index;
