export const changePage = ({ query, nop }) => async (dispatch) => {
  try {
    const res = await axios.get(`${PROXY}/api/post/${query}`);

    dispatch({
      type: CHANGE_PAGE,
      payload: res.data,
    });
  } catch (err) {}
};

export const loadingf = () => async (dispatch) => {
  dispatch({ type: LOADING });
};

export const deletePost = (id) => async (dispatch) => {
  try {
    const res = await axios.delete(`${PROXY}/api/post/${id}`);
    dispatch({
      type: DELETE_POST,
      payload: id,
    });
    dispatch(setAlert('Post removed', 'success'));
  } catch (err) {
    dispatch({
      type: POST_ERROR,
      payload: { msg: err.response.statusText, status: err.response.status },
    });
  }
};

