import React, { Fragment, useState } from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { login } from '../../actions/auth'; //===glxn定义

//下面两个login都去掉()才不报错。
// console.log(login() + 'login1');
const Login = ({ login, isAuthenticated }) => {
  //===glxn使用
  // console.log(login() + 'logim2');
  const [formData, setFormData] = useState({
    email: '',
    password: '',
  });

  const { email, password } = formData;

  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const onSubmit = (e) => {
    e.preventDefault();

    login({ email, password });
  };

  //redirect if logged in
  if (isAuthenticated) {
    return <Redirect to='/send' />;
  }

  return (
    <section className='container'>
      {/* <div className='alert alert-danger'>Invalid credentials</div> */}
      <h1 className='large text-primary'>登录 APP</h1>
      <p className='lead'>
        <i className='fas fa-user'></i> 登入账户
      </p>
      {/* 这里action似乎永远没用。成功了被redirect,失败了停在原地 */}
      <form className='form' action='/send' onSubmit={(e) => onSubmit(e)}>
        <div className='form-group'>
          <input
            type='email'
            placeholder='邮箱'
            name='email'
            value={email}
            onChange={(e) => {
              onChange(e);
            }}
            required
          />
        </div>
        <div className='form-group'>
          <input
            type='password'
            placeholder='密码'
            name='password'
            value={password}
            onChange={(e) => {
              onChange(e);
            }}
          />
        </div>
        <input type='submit' className='btn btn-primary' value='Login' />
      </form>
      <p className='my-1'>
        没有账户？ <Link to='/register'>注册</Link>
      </p>
      ;
    </section>
  );
};

Login.propTypes = {
  login: PropTypes.func.isRequired,
  isAuthenticated: PropTypes.bool,
};

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
});

export default connect(mapStateToProps, { login })(Login);
