import React, { Fragment, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Spinner from '../layout/spinner';
import { deletePost, getPost, loadingf } from '../../actions/post';
import { Link } from 'react-router-dom';
import Moment from 'react-moment';
import CommentForm from './commentForm';
import CommentItem from './commentItem';
import { PROXY } from '../../utils/consts';
import Image from './image';
import './post.css';

function Post({ auth, getPost, post: { post, loading }, match }) {
  useEffect(() => {
    // 和post === null效果相同; //不同。useeffect is called after render.https://reactjs.org/docs/hooks-effect.html
    const query = JSON.stringify({ id: match.params.id });
    getPost({ query });
  }, [getPost]);

  if (loading || post === null) {
    return <Spinner />;
  }
  //post===null气死我了
  else {
    const { _id, text, name, user, comments, postDate, imageUrl, type } = post;
    const images = [];
    imageUrl.forEach((el) =>
      images.push({
        original: el ? PROXY + '/' + el.url : null,
      })
    );
    return (
      <section className='container'>
        <Link to={`/posts/${type}`} className='btn'>
          返回
        </Link>
        <div className='post1 bg-white p-1 my-1'>
          <div>
            <Image items={images} />
          </div>
          <div>
            <p className='my-10'>{text}</p>
            <p className='post-date'>
              {name} 发布于{' '}
              <Moment format='YYYY/MM/DD  h:mm:ss a'>{postDate}</Moment>
              {!auth.loading && user === (auth.user ? auth.user._id : null) && (
                <span className='bs-1'>
                  <button
                    style={{
                      float: 'right',

                      background: 'white',
                      color: 'rgb(200,200,200)',
                    }}
                    type='button'
                    className='btn btn-danger'
                    onClick={() => deletePost(_id)}
                  >
                    删除
                  </button>
                  <button
                    style={{
                      float: 'right',

                      background: 'white',
                      color: 'rgb(200,200,200)',
                    }}
                    type='button'
                    className='btn btn-danger'
                    onClick={() => deletePost(_id)}
                  >
                    编辑
                  </button>
                </span>
              )}
            </p>
            {/* <Link to={`/posts/${type}/${_id}`} className='btn btn-primary'>
              查看详情
            </Link> */}
            {/* 两种模式，一种是link to, 在新route发送请求，另一种是button, click发送请求。如果Linkto然后click发送请求，而不在route useeffect发送请求，哪个好？ */}
          </div>
        </div>
        <CommentForm postId={post._id} />
        <div className='comment'>
          {post.comments.map((comment) => (
            <CommentItem
              key={comment._id}
              comment={comment}
              postId={post._id}
            />
          ))}
        </div>
      </section>
    );
  }
}

Post.propTypes = {
  getPost: PropTypes.func.isRequired,
  post: PropTypes.object.isRequired,
  auth: PropTypes.object.isRequired,
  loadingf: PropTypes.func.isRequired,
};

const mapStateToProps = (state) => ({ post: state.post, auth: state.auth });
export default connect(mapStateToProps, { getPost, loadingf })(Post);
